import styled from 'styled-components';

export const Wrap = styled.div`
  flex: 1 1 33%;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #fff;
  padding: 5px 15px;
  min-width: 260px;

  svg {
    color: red;
  }

  * {
    margin: 0 5px;
  }
`;
