import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { Col, Row } from 'react-bootstrap';

import { Footer, Banner } from '../../components';

import { CarDetailsStyled, Container, Destaq } from './styles';
import { getAllCars } from '../../features/cars/carsSlice';
// import { useFormatCurrency } from '../../hooks/useFormatCurrency';

const CarDetails = () => {
  const { id } = useParams();
  const dispatch = useDispatch();
  const carros = useSelector((state) => state.cars.cars);

  const [carro, setCarro] = useState(null);

  useEffect(() => {
    dispatch(getAllCars());
  }, [dispatch]);

  useEffect(() => {
    if (carros) {
      setCarro(carros.filter((carro) => carro.id === id)[0]);
    }
  }, [carros, id]);

  return (
    <CarDetailsStyled>
      {carro ? (
        <>
          <Banner data={carro} />
          <Destaq>
            <div>
              <h1>
                {carro.marca} - {carro.modelo}
              </h1>
              <p>xvdfg{carro.combustivel}</p>
            </div>

            <h2>
              {carro.preco}
              <span> EUR</span>
            </h2>
          </Destaq>
          <Container>
            <h1>Detalhes</h1>
            <Row>
              <Col lg={4} sm={6} xs={12}>
                <span>Marca</span> Mercedes-Benz
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Modelo</span> C 250
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Sub-modelo</span> Classe C Verso d Aut.
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Combustvel</span> Diesel
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Ms de Registo</span> Setembro 2016
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Quilmetros</span> 39 000 km
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Cilindrada</span> 2 143 cm3
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Potncia</span> 204 cv
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Segmento</span> Cabrio
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Cor</span> Preto
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Tipo de cor</span> Metalizado
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Tipo de Caixa</span> Automticad
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Nmero de Mudanas</span> 9
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>N de portas</span> 2d
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Lotao</span> 4d
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Classe do veculo</span> Classe 1d
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Traco</span> Traco traseira
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Emisses CO2</span> 121 g/klg 64
              </Col>
              <Col lg={4} sm={6} xs={12}>
                <span>Origem</span> Nacional
              </Col>
            </Row>

            <Row>
              <h2>Anotaes</h2>
              <Col>fdsfsdfsdf</Col>
            </Row>
          </Container>
        </>
      ) : (
        <Container className='container'>
          <h1>Viatura no localizada</h1>
        </Container>
      )}
      <Footer />
    </CarDetailsStyled>
  );
};

export default CarDetails;
